*{
    font-family: "Cascadia Code",Consolas,Monaco,"Andale Mono","Ubuntu Mono",monospace;
}
.remover{
    text-decoration:none;
}
.button-67 {
    align-items: center;
    background: #ffdcdc;
    border: 0;
    border-radius: 8px;
    
    box-sizing: border-box;
    color: #2a1f62;
    cursor: pointer;
    display: flex;
    font-family: "Cascadia Code",Consolas,Monaco,"Andale Mono","Ubuntu Mono",monospace;
    font-size: 1rem;
    justify-content: center;
    line-height: 1.5rem;
    padding: 1px;
    position: relative;
    text-align: left;
    transition: .2s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: pre;
    width: max-content;
    word-break: normal;
    word-spacing: normal;
  }
  

.button-70 {
    align-items: center;
    background: #ffdcdc;
    border: 0;
    border-radius: 8px;
    
    box-sizing: border-box;
    color: #2a1f62;
    cursor: pointer;
    display: flex;
    font-family: "Cascadia Code",Consolas,Monaco,"Andale Mono","Ubuntu Mono",monospace;
    font-size: 1rem;
    justify-content: center;
    line-height: 1.5rem;
    padding: 1px;
    position: relative;
    text-align: left;
    transition: .2s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: pre;
    width: max-content;
    word-break: normal;
    word-spacing: normal;
  }
  
  .button-67:hover {
    background: rgb(228, 228, 228);
    
  }
  .button-70:hover {
    background: #F5F5FA;
    
  }
  
  @media (min-width: 768px) {
    .button-67 {
      padding: 10px;
    }
  }

  @media (min-width: 768px) {
    .button-70 {
      padding: 25px;
    }
  }