table {
    border-collapse: collapse;
    max-width: 1400px;
    margin: 10px auto;
    table-layout: fixed;
    width: 100%;
  }
  #add{
    left: 60px;
    width: 222px;
  }
#logout{
    float: right;
    margin-right: 65px;
    width: 222px;
}
  thead th {
    font-weight: 400;
    background: #5c5c5c;
    color: #FFF;
  }
  tr {
    background: white;
    border-bottom: 1px solid #FFF;
    margin-bottom: 5px;
  }
  tr:hover {
    background: rgb(245, 245, 245);
  }
  
  tr:nth-child(even) {
    background: #e8eeef;
  }
  
  th, td {
    text-align: left;
    padding: 20px;
    font-weight: 300;
  }
  .notess{
    border: solid 1px #666;
    width: 150px;
    word-wrap: break-word;
  }
.others{
    border: solid 1px #666;
    width: 5px;
    word-wrap: break-word;
}
.createdat{
    border: solid 1px #666;
    width: 28px;
    word-wrap: break-word;
}

textarea {
    display: block;
    width: 980px;
    height: 40px;
    padding: 10px;
    font-weight: bold;
    font-size: 20px;
    color: #14213d;
    border: 2px solid #8d99ae;
    border-radius: 20px;
    margin: 20px auto;
    outline: none;

}

textarea {
    height: 350px;
    font-size: 18px;
}
textarea:hover{
    border: 2px solid pink;
}

#updateb{
    margin: auto;
    width: 480px;
    padding: 10px;
}

#titlee{
    padding-left: 300px;
}